.list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 80%;
    margin: 20px auto;
}

.list .tile {
    width: 20%;
    padding: 30px 10px;
    margin-top: 20px;
    border-radius: 5px;
    text-decoration: none;
    border: 1px solid grey;
}

.homepage-loader {
    height: 100vh;
    width: 100%;

}
.homepage-loader svg {
    position:absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    fill:cornflowerblue;
}

.homepage {
    position: absolute;
    width: 100%;
    animation-name: move-in-right;
    animation-duration: 2s;
    animation-fill-mode: forwards;
}

@keyframes move-in-right {
    from {
        left: -100%;
    }
    to {
        left: 0;
    }
}