.season-details {
    width: 100%;
}

.season-details .list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    width: auto;
}

.season-details .list div {
    width: 98%;
    margin: 10px auto;
    height: 80px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    border: 1px solid grey;
    border-radius: 5px;
    flex-wrap: wrap;
}

.season-details .list span,a{
   display: inline-block;
   width: 20%;
}

.season-details .list span:nth-of-type(1){
    width: 100%;
 }

 .mobile-only-block {
     display: block!important;
 }

 .mobile-only-inline-block {
    display: inline-block!important;
}

.desktop-only-block, .desktop-only-inline-block {
    display: none!important;
}



@media (min-width: 780px){
    
    .mobile-only-block, .mobile-only-inline-block {
        display: none!important;
    }

    .desktop-only-block {
        display: block!important;
    }
   
    .desktop-only-inline-block {
       display: inline-block!important;
   }

    .season-details {
        max-width: 80%;
        margin : 20px auto;
    }

    .season-details .list {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-around;
        width: 100%;
    }

    .season-details .list div {
        width: 90%;
    }

    .season-details .list span:nth-of-type(1){
        width: 20%;
     }
}




